import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import store from './store'
import '@/libs'
import router from './router'
import './config'
// 
require('./assets/sass/app.less')

// 
router.beforeEach((to, from, next) => {
  // 
  store.commit('dv_toggle_mode', '')
  next()
})

// 
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app-box')



